* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  list-style: none;
  font-family: sans-serif;
  border: none;
}

:root {
  --webColor: rgb(253,86,49);
  --darkBlack: rgb(31,27,45);
  --lightBlack: rgb(102,98,118);
  --white: #fff;
  --green: rgb(7,201,139);
  --red: rgb(242,60,73);
  --yellow: rgb(253,188,49);
  --webColorOpacity: rgba(253,86,49, 0.2);
  --boxShadowWeb: 3px 6px 5px 1px rgba(253,86,49,0.7);
  --boxShodowBlack: 0 0 15px 1px rgba(0,0,0,0.2);
}

h1,
h2 {
  color: var(--darkBlack);
}

a,
button {
  cursor: pointer;
}

p {
  font-size: 3em;
  color: var(--paragraf);
}